import React from "react";

import {
    Card,
    CardBody,
    CardSubtitle,
    CardText,
    CardHeader,
    Col
} from "reactstrap"; 

import { Fade } from "react-reveal";

const InterestsCard = ({ data, image }) => {
	return (
		<Col lg="6">
			<Fade bottom duration={1000} distance="40px">
				<Card style={{flex: 1}} className="shadow-lg--hover shadow mt-4">
					<CardBody className="py-5">
						<CardHeader className="section pb-0 bg-gradient-info my-5 text-center">
							<h3 style={{ color: 'white' }}>{data.name}</h3>
							<br/><br/>
						</CardHeader>
						<a href={data.github}>
							<img className="img-center" style={{ width: "250px"}} src = {image} />
						</a>
						<br/>
						<CardSubtitle>{data.desc}</CardSubtitle>
						<CardText className="description my-3 text-left">
							<ul>
								{
									data.descBullets ? 
									data.descBullets.map((desc) => {
										return <li key={desc}>{desc}</li>
									}) : null
								}
							</ul>
						</CardText>
					</CardBody>
				</Card>
			</Fade>
		</Col>
	);
};

export default InterestsCard;
